:root {
    --primary: #fff;
    --black: #242424;
}

.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424!important;
    color: var(--black)!important;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    border-radius: 4px;

}

.btn--outline{
    background-color: transparent;
    color: #fff!important;
    color: var(--primary)!important;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
    border-radius: 4px;

}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--primary:hover {
    background-color: transparent;
    color:#fff!important;
    color:var(--primary)!important;
    transition: all 0.3s ease-out;
}

.btn--outline:hover{
    background: #fff;
    background: var(--primary);
    color:#242424!important;
    color:var(--black)!important;
    transition: all 0.3s ease-out;
}

.btn--dark{
    background-color: transparent;
    color: black;
    padding: 8px 20px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
    border-radius: 4px;

}

.btn--dark:hover{
    background: black;
    color:white;
    transition: all 0.3s ease-out;

}
.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky!important;
  position: sticky!important;
  top: 0;
  z-index: 999;
  width:100%;
  overflow: none;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
}

.navbar-logo {
  /* color: #fff; */
  
  background: -webkit-linear-gradient(0deg,rgba(252,70,107,1), rgba(34,195,138,1));
  
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  justify-self: start;
  margin-left: 10%;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  font-weight: italic;
  padding-bottom:11px
  
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: flex;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  align-items: center;
  width: 60vw;
  justify-content: end;
  /* margin-right: 2rem; */
  float: right;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;

}

.navbar-logo:hover {
  /* border-bottom:  */
  border: 0px;
  border-bottom: 4px solid;
  border-image: -webkit-linear-gradient(0deg,rgba(252,70,107,1), rgba(34,195,138,1));
  border-image-slice: 1;
  transition: all 0.2s ease-out;
  

}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

#nav-item-logo{
  display:none;
}


.hide{
  display: block;
}
@media screen and (max-width: 640px) {
  .navbar-container {
    display: flex;
    
    height: 80px;
    width: 100%;
  } 
  .navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.0rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    width:100%;
    overflow: hidden;
  }
  .nav-menu {
    display: flex;
    grid-template-columns: repeat(4, auto);
    grid-gap: 5px;
    list-style: none;
    text-align: center;
    width: 100%;
    padding-left: 0%;
    justify-content:center;

    /* margin-right: 2rem; */

  }

  .hide{
    display: none;
  }
  #nav-item-logo{
    display: flex;

    background: -webkit-linear-gradient(0deg,rgba(252,70,107,1), rgba(34,195,138,1));
  
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .navbar-logo{
    display:none;
  }

  #nav-item-logo:hover {
    border: 0px;
    border-bottom: 4px solid;
    border-image: -webkit-linear-gradient(0deg,rgba(252,70,107,1), rgba(34,195,138,1));
    border-image-slice: 1;
    transition: all 0.2s ease-out;
    
  
  }

}

